import React from 'react';
import Post from './Post';
import css from './Home.module.css';
import publicUrl from '../utils/publicURL';

function Home(props) {
  const {store} = props;
 
  return (
    <div>
      {store.posts.sort((a,b)=> new Date(b.datetime) - new Date(a.datatime))
      .map(post =>
      <Post 
        user={post.user} 
        likes = {post.likes} 
        post = {post.post} 
        comments={post.comments}  
      />
    )}
    </div>
  );
}

export default Home;